import React from 'react';
import withBem from 'hoc/withBem';
import { useFocusOnMount, useInterval } from 'hooks';
import { Button } from 'components/Button/Button';

import './Promo.scss';

type PromoProps = BemProps;

const POPCORN_TYPES_COUNT = 4;

const Component: React.FC<PromoProps> = ({ bem }) => {
    const mountRef = useFocusOnMount<HTMLButtonElement>();
    const [currentIndex, setIndex] = React.useState(0);
    useInterval(() => setIndex(currentIndex === POPCORN_TYPES_COUNT - 1 ? 0 : currentIndex + 1), 3000);

    return (
        <div className={bem.modify({ [`type-${currentIndex + 1}`]: true })}>
            <div className={bem('glow')}>
                <div className={bem.modify({ [`type-${currentIndex + 1}`]: true }, 'glow-1')} />
                <div className={bem.modify({ [`type-${currentIndex + 1}`]: true }, 'glow-2')} />
                <div className={bem.modify({ [`type-${currentIndex + 1}`]: true }, 'glow-3')} />
                <div className={bem.modify({ [`type-${currentIndex + 1}`]: true }, 'glow-4')} />
            </div>
            <div className={bem('title')}>
                Попкорн и напитки
                <br />к просмотру фильмов
                <br />и сериалов
            </div>
            <div className={bem('subtitle')}>
                Доставим бесплатно в течении 15-30 минут для Москвы и Санкт-Петербурга
            </div>
            {Array.from(Array(POPCORN_TYPES_COUNT)).map((_, index) => (
                <div
                    key={index}
                    className={bem.modify({ [`type-${index + 1}`]: true, hidden: index !== currentIndex }, 'image')}
                />
            ))}
            <div className={bem('button-container')}>
                <Button className={bem('button')} shouldFocusOnMount>
                    Указать адрес
                </Button>
                <Button className={bem('button')}>Не сейчас</Button>
            </div>
        </div>
    );
};

export const Promo = withBem(Component, 'Promo');
