import { CatalogListItemProps } from 'pages/Catalog/ListItem/CatalogListItem';

import { AppState, Reducer } from '../constants';

const SCREEN_WIDTH = 1920;
const CARD_WIDTH = 392;
const CARD_GAP = 32;

const SCREEN_ITEM_COUNT = Math.floor(SCREEN_WIDTH / (CARD_WIDTH + CARD_GAP));

const LIST_OFFSET = (-1 * (SCREEN_WIDTH % (CARD_WIDTH + CARD_GAP))) / 2;

const ITEM_WIDTH = CARD_WIDTH + CARD_GAP;

const getListIndexOffset = ({ catalogPage: { selectedItemId, order, listIndexOffset } }: AppState) => {
    const selectedItemIndex = order.indexOf(selectedItemId);

    if (selectedItemIndex === -1) {
        return 0;
    }

    const scrollPageSize = 1;

    if (selectedItemIndex <= listIndexOffset) {
        return Math.max(0, listIndexOffset - scrollPageSize);
    }

    if (selectedItemIndex >= listIndexOffset + SCREEN_ITEM_COUNT - 1) {
        return Math.min(listIndexOffset + scrollPageSize, order.length - SCREEN_ITEM_COUNT);
    }

    return listIndexOffset;
};

const getWrapperXShift = ({ catalogPage: { listIndexOffset } }: AppState) =>
    LIST_OFFSET + listIndexOffset * ITEM_WIDTH - CARD_GAP / 2;

const getScreenIndex = ({ catalogPage: { listIndexOffset } }: AppState): number[] =>
    new Array(SCREEN_ITEM_COUNT).fill(0).reduce((result, _, index) => {
        result[index + 1] = index + listIndexOffset + 1;

        return result;
    }, [] as number[]);

const preparePrice = (price: number) => Math.ceil(price / 100);

const prepareBadge = ({ cart, products }: AppState, itemId: string): CatalogListItemProps['badge'] => {
    const cartItem = cart.itemsMap[itemId];
    const productItem = products.itemsMap[itemId];

    if (productItem.quantityLeft === 0) {
        return { type: 'sold-out' };
    }

    if (cartItem && cartItem.count === productItem.quantityLeft) {
        return { type: 'few-left' };
    }

    return undefined;
};

const calculateCount = ({ cart, catalogPage }: AppState, itemId: string) => {
    const cartItem = cart.itemsMap[itemId];

    return cartItem ? cartItem.count : 0;
};

const calculateButtonDisabled = ({ cart, products }: AppState, itemId: string) => {
    const productItem = products.itemsMap[itemId];
    const cartItem = cart.itemsMap[itemId];

    return cartItem && cartItem.count === productItem.quantityLeft;
};

const prepareItem = (state: AppState, itemId: string, index: number) => {
    const { products, cart, catalogPage } = state;
    const productItem = products.itemsMap[itemId];
    const cartItem = cart.itemsMap[itemId];

    return {
        shouldFocusOnMount: catalogPage.selectedItemId === itemId,
        imageUrl: productItem.imageUrl,
        name: productItem.name,
        id: itemId,
        price: preparePrice(productItem.price),
        index: index + 1,
        quantityLeft: productItem.quantityLeft,
        count: cartItem && cartItem.count,
        badge: prepareBadge(state, itemId),
        buttonDisabled: calculateButtonDisabled(state, itemId),
        disabled: productItem.quantityLeft === 0,
    };
};

const prepareCatalogItemsMap = (state: AppState) => {
    const { products } = state;

    return Object.keys(products.itemsMap).reduce((acc, itemId, index) => {
        acc[itemId] = prepareItem(state, itemId, index);

        return acc;
    }, {} as AppState['catalogPage']['itemsMap']);
};

export const catalogPage: Reducer = (state, action) => {
    const { products, catalogPage } = state;

    switch (action.type) {
        case 'SHOW_ITEMS': {
            catalogPage.order = Object.keys(products.itemsMap);

            const [firstItemId] = catalogPage.order;
            catalogPage.selectedItemId = firstItemId;

            catalogPage.itemsMap = prepareCatalogItemsMap(state);
            catalogPage.screenIndex = getScreenIndex(state);

            break;
        }

        case 'ADD_ITEM': {
            const { item: itemId } = action.payload;
            const catalogItem = catalogPage.itemsMap[itemId];

            catalogItem.count = calculateCount(state, itemId);
            catalogItem.buttonDisabled = calculateButtonDisabled(state, itemId);
            catalogItem.badge = prepareBadge(state, itemId);

            break;
        }

        case 'REMOVE_ITEM': {
            const { item: itemId } = action.payload;
            const catalogItem = catalogPage.itemsMap[itemId];

            catalogItem.count = calculateCount(state, itemId);
            catalogItem.buttonDisabled = calculateButtonDisabled(state, itemId);
            catalogItem.badge = prepareBadge(state, itemId);

            break;
        }

        case 'REMOVE_ALL': {
            catalogPage.itemsMap = prepareCatalogItemsMap(state);

            break;
        }

        case 'catalog/SELECT_ITEM': {
            catalogPage.selectedItemId = action.payload;
            catalogPage.listIndexOffset = getListIndexOffset(state);
            catalogPage.wrapperXShift = getWrapperXShift(state);
            catalogPage.screenIndex = getScreenIndex(state);

            break;
        }

        default:
            break;
    }
};
