import 'core-js';
import React from 'react';
import ReactDOM from 'react-dom';
import { Root } from 'components/Root/Root';

import 'tools/new-spatial-navigation';

export const renderApp = (App: React.FC) => {
    ReactDOM.render(<App />, document.getElementById('application_root'));
};

renderApp(Root);

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./components/Root/Root', () => {
        // eslint-disable-next-line
        const { Root } = require('./components/Root/Root.tsx');
        renderApp(Root);
    });
}
