import React from 'react';
import { useDispatch, useSelector } from 'hooks';
import { ROUTES } from 'store/constants/router';
import { push } from 'connected-react-router';
import { throttle } from 'tools';
import withBem from 'hoc/withBem';

import './HeaderAddress.scss';

type HeaderAddressProps = BemProps;

const Component: React.FC<HeaderAddressProps> = ({ bem }) => {
    const dispatch = useDispatch();
    const {
        // eslint-disable-next-line @typescript-eslint/camelcase
        address: { address_string },
    } = useSelector(({ user }) => user);
    // eslint-disable-next-line @typescript-eslint/camelcase
    const userAddress = address_string;

    return (
        <div className={bem()} tabIndex={0} onClick={throttle(() => dispatch(push(ROUTES.ADDRESS_SIMPLE)), 200)}>
            <div className={bem('icon')} />
            <div className={bem('text')}>{userAddress}</div>
        </div>
    );
};

export const HeaderAddress = withBem(Component, 'HeaderAddress');
