import React from 'react';
import withBem from 'hoc/withBem';

import './Header.scss';

interface HeaderProps extends BemProps {
    className?: string;
    title: string;
}

const Component: React.FC<HeaderProps> = ({ bem, title, children, className }) => (
    <div className={bem.all(bem(), className || '')}>
        {window.history.length ? <span className={bem('back')} /> : ''}
        <div className={bem('section')}>
            <div className={bem('logo')} />
            <div className={bem('title')}>{title}</div>
        </div>
        <div className={bem('section')}>{children}</div>
    </div>
);

export const Header = withBem(Component, 'Header');
