import { CartListItemViewProps } from 'pages/Cart/ListItem/CartListItem';
import { ButtonProps } from 'components/Button/Button';
import { splitByThousands } from 'tools';

import { Reducer, AppState, initialState } from '../constants';

const selectProductById = (state: AppState, itemId: string) => state.products.itemsMap[itemId];
const selectCartItemById = (state: AppState, itemId: string) => state.cart.itemsMap[itemId];

const ITEM_HEIGHT = 152;

const getWrapperYShift = ({ cartPage: { selectedItemIndex, order } }: AppState) => {
    if (selectedItemIndex === -1) {
        return 0;
    }

    // так как индекс начинается с 0, то расчеты бы имели смысл начиная
    // со второго элемента (индекс = 1), но нам нужно начинать скролл с
    // 3-го элемента, поэтому сдвигаем значение на 1
    let relatedIndex = Math.max(0, selectedItemIndex - 1);

    // ограничиваем максимальное значение индекса
    relatedIndex = Math.min(Math.max(0, order.length - 4), relatedIndex);

    // домножаем отступ на коэффициент высоты айтема
    return relatedIndex * ITEM_HEIGHT;
};

const prepareCartPageItem = (state: AppState, itemId: string): CartListItemViewProps | undefined => {
    const { imageUrl, name, price, quantityLeft } = selectProductById(state, itemId);
    const cartItem = selectCartItemById(state, itemId);

    if (!cartItem) {
        return;
    }

    return {
        imageUrl,
        name,
        price: Math.ceil(price / 100),
        count: cartItem.count,
        disabled: quantityLeft === 0,
        moreButtonDisabled: quantityLeft === cartItem.count,
    };
};

const prepareMakePurchaseButtonProps = (store: AppState): ButtonProps => {
    const {
        cart: { totalPrice, minimalPrice },
        cartPage: { isOrderStatusWaiting },
    } = store;

    return {
        disabled: isOrderStatusWaiting || totalPrice < minimalPrice,
    };
};

const prepareToMinimumPrice = ({ cart: { minimalPrice, totalPrice } }: AppState) =>
    Math.max(0, minimalPrice - totalPrice);

const prepareTotalPrice = ({ cart: { totalPrice } }: AppState) => splitByThousands(Math.ceil(totalPrice / 100));

const prepareTopGradientHidden = ({ cartPage: { selectedItemIndex } }: AppState) => selectedItemIndex === 0;

const prepareBottomGradientHidden = ({ cartPage: { selectedItemIndex, order } }: AppState) =>
    selectedItemIndex === order.length - 1;

export const cartPage: Reducer = (state, action) => {
    const { cart, cartPage } = state;

    switch (action.type) {
        case 'ADD_ITEM': {
            const { item: itemId } = action.payload;
            const cartPageItem = prepareCartPageItem(state, itemId);

            if (cartPageItem) {
                cartPage.itemsMap[itemId] = cartPageItem;
            }

            cartPage.order = cart.order;
            cartPage.totalPrice = prepareTotalPrice(state);
            cartPage.toMinimumPrice = prepareToMinimumPrice(state);

            break;
        }

        case 'REMOVE_ITEM': {
            const { item: itemId } = action.payload;
            const cartPageItem = prepareCartPageItem(state, itemId);

            if (cartPageItem) {
                cartPage.itemsMap[itemId] = cartPageItem;
            } else {
                delete cartPage.itemsMap[itemId];
            }

            cartPage.order = cart.order;
            cartPage.totalPrice = prepareTotalPrice(state);
            cartPage.toMinimumPrice = prepareToMinimumPrice(state);
            cartPage.wrapperYShift = getWrapperYShift(state);
            cartPage.makePurchaseButton = prepareMakePurchaseButtonProps(state);

            break;
        }

        case 'REMOVE_ALL': {
            state.cartPage = initialState.cartPage;
            cartPage.makePurchaseButton = prepareMakePurchaseButtonProps(state);

            break;
        }

        case 'cart/SELECT_ITEM': {
            cartPage.selectedItemIndex = cartPage.order.indexOf(action.payload);

            cartPage.wrapperYShift = getWrapperYShift(state);
            cartPage.topGradientHidden = prepareTopGradientHidden(state);
            cartPage.bottomGradientHidden = prepareBottomGradientHidden(state);

            break;
        }

        case 'MAKE_PURCHASE': {
            cartPage.isOrderStatusWaiting = true;

            cartPage.makePurchaseButton = prepareMakePurchaseButtonProps(state);

            break;
        }

        case 'VALIDATION_OK': {
            cartPage.isOrderStatusWaiting = false;

            cartPage.makePurchaseButton = prepareMakePurchaseButtonProps(state);

            break;
        }

        default:
            break;
    }
};
