export const splitByThousands = (data: number) =>
    String(data)
        .split('')
        .reduceRight((acc, item, index, arr) => {
            const indexFromEnd = index - (arr.length - 1);

            if (indexFromEnd % 3 === 0) {
                acc = ` ${acc}`;
            }

            return item + acc;
        }, '');

export const throttle = (callback: any, timeout: number) => {
    let timeoutId = 0;

    return () => {
        if (timeoutId) {
            return;
        }

        timeoutId = setTimeout(() => {
            timeoutId = 0;
            callback();
        }, timeout);
    };
};
