import React from 'react';
import { useSelector, useDispatch } from 'hooks';
import { SHOW_ITEMS_MOCK } from 'mocks';
import { askForStock } from 'store/actions';
import { Header } from 'components/Header/Header';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { HeaderCart } from 'components/HeaderCart/HeaderCart';

import { CatalogList } from './List/CatalogList';
import { CatalogSkeleton } from './Skeleton/CatalogSkeleton';

export const Catalog: React.FC = () => {
    const dispatch = useDispatch();
    const {
        cart,
        catalogPage: { order },
    } = useSelector(({ cart, products, catalogPage }) => ({
        cart,
        products,
        catalogPage,
    }));

    React.useEffect(() => {
        dispatch(askForStock());
    }, [dispatch]);

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'SHOW_ITEMS',
                    stock: SHOW_ITEMS_MOCK,
                },
            });

            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'ADD_ITEM',
                    item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                    count: '2',
                },
            });
        }
    }, []);

    return (
        <>
            <Header title="Самокат">
                <HeaderAddress />
                {cart.totalCount > 0 ? <HeaderCart /> : ''}
            </Header>
            {order.length ? <CatalogList /> : <CatalogSkeleton />}
        </>
    );
};
