import React from 'react';
import { Header } from 'components/Header/Header';
import { Button } from 'components/Button/Button';
import { useSelector } from 'hooks';
import withBem from 'hoc/withBem';

import './NoArea.scss';

type NoAreaProps = BemProps;

const Component: React.FC<NoAreaProps> = ({ bem }) => {
    const { address } = useSelector(({ user }) => user);

    return (
        <div className={bem()}>
            <Header className={bem('header')} title="Адрес доставки" />
            <div className={bem('container')}>
                <div className={bem('badge')}>
                    <div className={bem('badge-body')} />
                    <div className={bem('badge-kernel')} />
                    <div className={bem.modify({ big: true }, 'badge-shadow')} />
                    <div className={bem.modify({ small: true }, 'badge-shadow')} />
                </div>
                <div className={bem('title')}>{address}</div>
                <div className={bem('subtitle')}>К сожалению, пока не доставляем по этому адресу</div>
                <Button shouldFocusOnMount className={bem('button')}>
                    Изменить
                </Button>
            </div>
            <div className={bem('background')} />
        </div>
    );
};

export const NoArea = withBem(Component, 'NoArea');
