import { Reducer } from '../constants';

export const user: Reducer = (state, action) => {
    switch (action.type) {
        case 'UPDATE_USER': {
            state.user = { ...state.user, ...action.payload };

            break;
        }

        case 'GO_TO_CATALOG': {
            state.user = action.payload.address;

            break;
        }

        default:
            break;
    }
};
