import React from 'react';
import styled from 'styled-components';
import { throttle } from 'tools';
import { updateUser, makePurchase } from 'store/actions';
import { useDispatch, useSelector } from 'hooks';
import { Button } from 'components/Button/Button';

const StyledCartInfo = styled.div`
    position: relative;
    display: flex;
    padding-top: 48px;
    box-sizing: border-box;
    flex-direction: column;
    align-items: center;
    width: 776px;
`;

const StyledBadge = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 132px;
    height: 124px;
`;

const StyledBadgeBody = styled.div`
    position: relative;
    width: 132px;
    height: 86px;
    background: #e12d39;
    border-radius: 108px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const StyledBadgeName = styled.div`
    color: #fff;
    height: 34px;
    font-size: 32px;
    font-weight: 600;
    line-height: 34px;
    text-align: center;
`;

const StyledBadgeDescription = styled.div`
    height: 20px;
    color: rgba(255, 255, 255, 0.8);
    font-size: 24px;
    font-weight: 500;
    line-height: 20px;
    text-align: center;
`;

const StyledBadgeKernel = styled.div`
    height: 34px;
    width: 4px;
    background: #e12d39;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
`;

