import React from 'react';
import withBem from 'hoc/withBem';
import styled from 'styled-components';
import { useSelector } from 'hooks';

import { CatalogListItem } from '../ListItem/CatalogListItem';

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollLeft = 0;
};

const StyledCatalogList = styled.div`
    margin-top: 72px;
    height: 640px;
    overflow: hidden;
`;

const StyledContainer = styled.div`
    position: relative;
    height: 660px;
    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

interface StyledWrapperProps {
    XShift: number;
}
const StyledWrapper = styled.div<StyledWrapperProps>`
    display: flex;
    white-space: nowrap;
    padding: 24px 0 44px;
    height: 660px;
    box-sizing: border-box;
    transition: transform 0.4s ease-in-out;
    transform: translateX(${({ XShift }) => -1 * XShift}px);
`;

const Component: React.FC = () => {
    const { itemsMap, order, wrapperXShift, screenIndex } = useSelector(({ catalogPage }) => catalogPage);
    const items = React.useMemo(() => order.map((itemId) => itemsMap[itemId]), [order, itemsMap]);

    return (
        <StyledCatalogList>
            <StyledContainer onScroll={preventScroll}>
                <StyledWrapper XShift={wrapperXShift}>
                    {items.map((item) => (
                        <CatalogListItem key={item.id} {...item} index={screenIndex.indexOf(item.index)} />
                    ))}
                </StyledWrapper>
            </StyledContainer>
        </StyledCatalogList>
    );
};

export const CatalogList = withBem(Component, 'CatalogList');
