/* eslint-disable no-underscore-dangle */
/* eslint-disable @typescript-eslint/camelcase */
import { createStore as createReduxStore, Middleware, applyMiddleware, compose, StoreEnhancer, AnyAction } from 'redux';
import { routerMiddleware, push, goBack, go, replace } from 'connected-react-router';
import { createHashHistory } from 'history';

import { USER_MOCK } from '../mocks';

import { AssistantSdkCommand } from './constants/native';
import { AssistantSdkServerAction } from './constants/server';
import { ROUTES } from './constants/router';
import { AppState, AppAction, AppDispatch } from './constants';
import { getRootReducer } from './reducers';
import * as actions from './actions';

const serverActionMiddleware: Middleware<{}, AppState, AppDispatch> = () => (next) => (action: AppAction) => {
    switch (action.type) {
        case 'ASK_FOR_STOCK': {
            window.AssistantClient.sendData({ action_id: action.type });
            break;
        }
        case 'ASK_FOR_ORDER_STATUS': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'VALIDATION_OK',
                            status: true,
                            // eslint-disable-next-line @typescript-eslint/camelcase
                            order_id: '364528',
                        },
                    });
                }, 1000);
            }

            break;
        }
        case 'MAKE_PURCHASE': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'ASK_AGAIN_FOR_ORDER_STATUS',
                        },
                    });
                }, 1000);
            }

            break;
        }
        case 'GO_TO_CART':
            window.AssistantClient.sendData({ action_id: action.type });
            break;
        default:
            break;
    }

    return next(action);
};

export const history = createHashHistory();
const enhancers: StoreEnhancer[] = [];
const middlewares: Middleware[] = [routerMiddleware(history), serverActionMiddleware];

if (process.env.NODE_ENV !== 'production') {
    if (typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION__) {
        enhancers.push(window.__REDUX_DEVTOOLS_EXTENSION__());
    }
}

// TODO: научиться инитить приложение на основе рекавери стейта
// https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-42
export const store = createReduxStore<AppState, AppAction, {}, {}>(
    getRootReducer(history),
    compose(applyMiddleware(...middlewares), ...enhancers),
);
const { dispatch } = store;

if (process.env.NODE_ENV !== 'production') {
    window.__store = store;
}

export const dispatchToReduxStore = ({ smart_app_data }: AssistantSdkCommand) => {
    switch (smart_app_data.command) {
        case 'SHOW_ITEMS':
            dispatch(actions.showItems(smart_app_data));
            break;
        case 'ADD_ITEM':
            dispatch(actions.addItem(smart_app_data));
            break;
        case 'REMOVE_ALL':
            dispatch(actions.removeAll(smart_app_data));
            break;
        case 'REMOVE_ITEM':
            dispatch(actions.removeItem({ ...smart_app_data, shouldRemoveMention: true }));
            break;
        case 'GO_TO_CATALOG':
            dispatch(actions.goToCatalog(smart_app_data));

            // чтобы при перезагрузке страницы постоянно не переходить в каталог
            // if (process.env.REACT_APP_ENVIRONMENT !== 'development') {
            dispatch(push(ROUTES.CATALOG));
            // }
            break;
        case 'GO_TO_NO_AREA': {
            dispatch(push(ROUTES.NO_AREA));
            break;
        }
        case 'GO_TO_VALIDATION': {
            dispatch(push(ROUTES.CART));
            break;
        }
        case 'VALIDATION_OK': {
            dispatch(actions.validationOk(smart_app_data));

            // for (let i = 0; i < history.length; i++) {
            //     window.history.back();
            //     window.history.replaceState(null, '');
            // }

            dispatch(go(history.length * -1 + 1));

            // dispatch(push(ROUTES.STATUS));
            dispatch(replace(ROUTES.STATUS));
            break;
        }
        case 'GO_TO_WAIT': {
            dispatch(actions.goToWait(smart_app_data));
            dispatch(actions.askForOrderStatus());
            break;
        }
        case 'GO_BACK': {
            dispatch(actions.goBack(smart_app_data));
            dispatch(goBack());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS': {
            dispatch(actions.askAgainForOrderStatus(smart_app_data));
            setTimeout(() => dispatch(actions.askForOrderStatus()), 1000);
            break;
        }
        default:
            break;
    }
};

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./reducers', () => {
        // eslint-disable-next-line
        const { getRootReducer } = require('./reducers');
        const rootReducer = getRootReducer(history);

        store.replaceReducer(rootReducer);
    });
}

const castFromStore = (): AppStateInterface => {
    const { cart, user, router } = store.getState();

    return {
        foodtech: {
            currentScreen: router.location.pathname.substring(1),
            products: Object.keys(cart.itemsMap).map((itemId) => ({
                id: itemId,
                quantity: cart.itemsMap[itemId].count,
            })),
            totalPrice: {
                value: cart.totalPrice,
                discountValue: cart.totalPrice,
            },
            address: user.address,
        },
    };
};

window.AssistantClient = window.AssistantClient || {};

if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
    // eslint-disable-next-line no-underscore-dangle
    window.__inbox = window.__inbox || [];
    // eslint-disable-next-line no-underscore-dangle
    window.__outbox = window.__outbox || [];
}

/**
 * Assistand SDK API implementation:
 */
window.AssistantClient.onStart = function onStart() {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke onStart callback');
    }
};

window.AssistantClient.onData = function onData(command: AssistantSdkCommand) {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        // eslint-disable-next-line no-underscore-dangle
        window.__inbox.push(command);
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`server command: ${JSON.stringify(command)}`);
    }

    dispatchToReduxStore(command);
};

window.AssistantClient.sendData = (message: AssistantSdkServerAction) => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        // eslint-disable-next-line no-underscore-dangle
        window.__outbox.push({ message, store: castFromStore() });
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`sendData: ${JSON.stringify(message)}`);
    }

    if (window.AssistantHost && typeof window.AssistantHost.sendData === 'function') {
        window.AssistantHost.sendData(JSON.stringify(message), null);
    }
};

window.AssistantClient.onRequestState = () => {
    const state = castFromStore();
    const stateString = JSON.stringify(state);

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke request state callback', stateString);
    }

    if (window.AssistantHost && typeof window.AssistantHost.updateState === 'function') {
        window.AssistantHost.updateState(stateString);
    }

    // return stateString;
};

if (window.AssistantHost && typeof window.AssistantHost.ready === 'function') {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('call ready native method');
    }

    window.AssistantHost.ready();
}

// эмуляция первой команды от бэка
if (process.env.REACT_APP_ENVIRONMENT === 'development') {
    setTimeout(() => {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line @typescript-eslint/camelcase
            smart_app_data: {
                command: 'GO_TO_CATALOG',
                // eslint-disable-next-line @typescript-eslint/camelcase
                address: USER_MOCK,
            },
        });
    }, 100);
}
