import React from 'react';
import styled, { css } from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route, Router } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { store, history } from 'store';
import { ROUTES } from 'store/constants/router';
import { Catalog } from 'pages/Catalog/Catalog';
import { Cart } from 'pages/Cart/Cart';
import { Status } from 'pages/Status/Status';
import { Promo } from 'pages/Promo/Promo';
import { NoArea } from 'pages/NoArea/NoArea';
import { ScreenSaver } from 'pages/ScreenSaver/ScreenSaver';
import { AddressSimple } from 'pages/AddressSimple/AddressSimple';

import 'style/global.scss';

const isStaging = process.env.REACT_APP_ENVIRONMENT === 'staging';

const StyledWrapper = styled.div`
    transform: ${isStaging ? 'translate(-580px, -310px)' : ''};
`;

// эти костыли со стилями нужны для того, чтобы верстка норм смотрелась на планшете
// там разрешение экрана ~600X300 px
const extraContainerStyles = isStaging
    ? css`
          transform: scale(0.4);
          height: 1080px;
          width: 1920px;
      `
    : '';

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: #001124;

    ${extraContainerStyles}
`;

export const Root: React.FC = () => (
    <ReduxProvider store={store}>
        <ConnectedRouter history={history}>
            <StyledWrapper>
                <StyledContainer>
                    <Switch>
                        <Route path={ROUTES.CATALOG}>
                            <Catalog />
                        </Route>
                        <Route path={ROUTES.CART}>
                            <Cart />
                        </Route>
                        <Route path={ROUTES.STATUS}>
                            <Status />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE}>
                            <AddressSimple />
                        </Route>
                        <Route path={ROUTES.PROMO}>
                            <Promo />
                        </Route>
                        <Route path={ROUTES.NO_AREA}>
                            <NoArea />
                        </Route>
                        <Route path={ROUTES.SCREEN_SAVER}>
                            <ScreenSaver />
                        </Route>
                    </Switch>
                </StyledContainer>
            </StyledWrapper>
        </ConnectedRouter>
    </ReduxProvider>
);
